import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { MdDialog } from "@angular/material";
import 'rxjs/add/operator/switchMap';

import { PoliciesComponent } from "app/pages/person-info/policies/policies.component";
import { Persona } from "app/pages/person-info/shared/persona";
import { PersonQuery } from "app/pages/person-info/shared/person-query";

@Component({
    selector: 'person-info',
    templateUrl: 'person-info.component.html'
})

export class PersonInfoComponent implements OnInit {
    placa: string;
    query: PersonQuery;
    person: Persona;
    acceptPolicies: boolean;


    departamentos = [
        { value: '1', label: 'Lima' },
        { value: '2', label: 'Arequipa' },
        { value: '3', label: 'La Libertad' }
    ];

    provincias = [
        { value: '1', label: 'Lima' },
        { value: '2', label: 'Barranca' },
        { value: '3', label: 'Cajatambo' }
    ];

    distritos = [
        { value: '1', label: 'Lima' },
        { value: '2', label: 'Miraflores' },
        { value: '3', label: 'San Isidro' }
    ];


    constructor(private route: ActivatedRoute, public dialog: MdDialog, private router: Router) { }

    ngOnInit() {
        this.route.queryParamMap
            .subscribe(param => {
                let tipoDoc = param.get('tipo_documento');
                let doc = param.get('documento');
                this.query = { numeroDocumento: doc, tipoDocumento: tipoDoc };
                this.loadPerson();
            });
    }

    loadPerson() {
        this.person = {
            tipoDocumento: this.query.tipoDocumento,
            numeroDocumento: this.query.numeroDocumento,
            nombres: 'Lorenzo',
            apellidoPaterno: 'Palacios',
            apellidoMaterno: 'Quispe',
            celular: '989898781',
            correo: 'lorenzo@latinmail.com',
            departamento: '1',
            direccion: 'Av las magnolias 387 La Victoria',
            distrito: '1',
            provincia: '1'
        }
    }

    viewPolicies() {
        this.dialog.open(PoliciesComponent);
    }

    verCobertura() {
        let data = JSON.parse(sessionStorage.getItem('dt'));
        data.ct = this.person;
        sessionStorage.setItem('dt', JSON.stringify(data));
        this.router.navigate(['/cobertura']);
    }
}


